import React from 'react';
// import { renderRoutes } from 'react-router-config';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
// import routes from './routes';
import load from './load';

const Page1 = load('page1');   // 测试页面
const Index = load('index');   // 首页
const NotFound = load('not_found');   // 首页

// BrowserRouter , 上线 nginx 要作相应配置  https://www.cnblogs.com/sunLemon/p/9020153.html
export default (
    <Router>
        <Switch>
            <Route exact path="/" component={Index}/>
            <Route exact path="/page1/:id" component={Page1}/>
            <Route path="*" component={NotFound} />
        </Switch>
    </Router>
);



